.fade {}
.fade-enter {
	-webkit-animation: fade .5s ease;
	        animation: fade .5s ease;
}
.fade-leave {
	-webkit-animation: fade .5s ease reverse;
	        animation: fade .5s ease reverse;
}

@-webkit-keyframes fade {
	to {
		opacity: 1;
	}
	from {
		opacity: 0;
	}
}

@keyframes fade {
	to {
		opacity: 1;
	}
	from {
		opacity: 0;
	}
}


